.Santa {
  display: flex;
  flex-direction: column;
}

.Santa-img {
  width: 10vw;
  min-width: 150px;
  height: auto;
  align-self: center;
  margin-top: 3rem;
}

.Santa-speechbubble {
  margin-top: 15px;
  position: relative;
  max-width: 30em;
  background-color: #fff;
  padding: 1.125em 1.5em;
  border-radius: 1rem;
  box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.3),
    0 0.0625rem 0.125rem rgba(0, 0, 0, 0.2);
  margin-left: auto;
  margin-right: auto;
  word-wrap: break-word;
}

.Santa-speechbubble.new-message {
  animation: breathing 1s ease-in normal 1;
}

.Santa-speechbubble::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  bottom: 100%;
  border: 0.75rem solid transparent;
  border-top: none;
  border-bottom-color: #fff;
  filter: drop-shadow(0 -0.0625rem 0.0625rem rgba(0, 0, 0, 0.1));
  left: 50%;
  transform: translateX(-50%);
}

@-webkit-keyframes breathing {
  0% {
    -webkit-transform: scale(.8);
    transform: scale(.8);
  }

  25% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  60% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes breathing {
  0% {
    -webkit-transform: scale(.8);
    -ms-transform: scale(.8);
    transform: scale(.8);
  }

  25% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }

  60% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }

  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }
}
